.main {
    border: 1px solid black;

    .content {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 12rem;
    }
    
    h2 {
        font-weight: bold;
        font-size: 1.2rem;
    }

    &:hover {
        background-color: #dfdfdf;
        cursor: pointer;
    }
}

